import Vue from "vue";
import Router from "vue-router";
import AutoGuid from "./components/AutoGuid";
import AboutUs from "./components/AboutUs";
import NotFound from "./components/NotFound";
import VueMeta from "vue-meta";

Vue.use(Router);
Vue.use(VueMeta);

export default new Router({
  routes: [
    {
      path: "*",
      component: NotFound
    },
    {
      path: "/",
      name: "AutoGuid",
      component: AutoGuid
    },
    {
      path: "/aboutus",
      name: "AboutUs",
      component: AboutUs
    }
  ]
});
