<template>
  <v-card :outlined="isOutlined" id="lastFive">
    <v-card-title>{{ title }} <export-to-csv :uuids-list="uuidsListReverse"
    /></v-card-title>
    <v-card-text>
      <v-list>
          <div v-for="(data, index) in uuidsListReverse" :key="data">
            <v-divider v-if="index > 0" :inset="true"></v-divider>
            <v-list-item :key="data" v-clipboard="data" link>
              <v-list-item-action>
                <v-icon>mdi-content-copy</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title v-html="data"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </div>
      </v-list>
    </v-card-text>
  </v-card>
</template>

<script>
import ExportToCsv from "./ExportToCSV";

export default {
  name: "LastFiveUuids",
  components: {
    ExportToCsv
  },
  props: {
    uuidsList: {
      type: Array,
      default: () => []
    },
    title: {
      type: String,
      default: "Last 5 Auto-generated UUID's (click to copy)"
    },
    isOutlined: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      maxItemCount: 5,
      localUuidList: this.uuidsList.reverse(),
    };
  },
  computed: {
    uuidsListReverse() {
      return this.localUuidList.slice().reverse();
    }
  }
};
</script>
<style scoped>
#lastFive .v-card__text {
  max-height: 378px;
  overflow-y: auto;
}
</style>
