<template>
  <v-card :loading="loading">
    <v-row>
      <v-overlay :value="overlay">
        <v-progress-circular indeterminate size="64"></v-progress-circular>
      </v-overlay>
    </v-row>
    <v-card-title class="headline">Bulk Export/Generate UUID's</v-card-title>
    <v-card-subtitle>upto 2000 UUID's</v-card-subtitle>
    <v-card-text class="d-flex mb-6 flex-wrap">
      <span>
        <v-form v-model="isValid" @submit="generate">
          <v-text-field
            label="How many?"
            :outlined="true"
            v-model="userRequestedCount"
            :dense="true"
            type="number"
            :min="minExportItems"
            :rules="numberRules"
          ></v-text-field>
        </v-form>
      </span>
      <v-btn class="ml-4" color="primary" :disabled="!isValid" @click="generate"
        >Generate</v-btn
      >
      <v-tooltip top class="clearfix">
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            class="mx-4 ml-4"
            icon
            color="primary"
            :disabled="!isValid"
            v-bind="attrs"
            v-on="on"
            @click="exportData"
          >
            <v-icon>mdi-download</v-icon>
          </v-btn>
        </template>
        <span>Export</span>
      </v-tooltip>
      <div>
        <last-five-uuids
          v-if="generateClicked"
          title="Generated UUIDs (Click to copy)"
          :uuids-list="generatedList"
          :is-outlined="true"
        ></last-five-uuids>
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
import uuid from "uuid/v4";
import LastFiveUuids from "./LastFiveUuids";

export default {
  name: "BulkExport",
  components: {
    LastFiveUuids
  },
  data() {
    return {
      userRequestedCount: 10,
      maxExportItems: 2000,
      minExportItems: 1,
      upperCase: false,
      isValid: true,
      generatedList: [],
      generateClicked: false,
      loading: false,
      overlay: false,
      numberRules: [
        v => !!v || "value is required!",
        v =>
          v >= this.minExportItems ||
          "number must not be less than" + this.minExportItems,
        v =>
          v <= this.maxExportItems ||
          `number must not be greater than ${this.maxExportItems}`
      ]
    };
  },
  methods: {
    exportData() {
      console.time("time");
      let data = this.generateData();
      this.exportToCSV(data);
      console.timeEnd("time");
    },
    exportToCSV(content) {
      let csvContent = "data:text/csv;charset=utf-8,";
      csvContent += "Bulk exported - " + content.length + " UUID's, \n";
      csvContent += content.map(uid => uid + ",").join("\n");
      const link = document.createElement("a");
      link.setAttribute("href", csvContent);
      link.setAttribute("download", "Bulk export - uuids.csv");
      link.click();
    },
    generateData() {
      let data = [];
      const count =
        parseInt(this.userRequestedCount) > this.maxExportItems
          ? this.maxExportItems
          : parseInt(this.userRequestedCount);

      for (let i = 0; i < count; i++) {
        data.push(uuid());
      }
      return data;
    },
    generate() {
      this.overlay = true;
      this.generateClicked = true;
      setTimeout(() => {
        var d = this.generateData();
        this.generatedList.splice(0, this.generatedList.length);
        this.chunkArrayAndUpdate(d, 10);
        this.overlay = false;
      }, 20);
    },
    chunkArrayAndUpdate(data, size) {
      for (let i = 0; i < data.length; i += size) {
        const chunk = data.slice(i, i + size);
        this.$nextTick(() => {
          this.generatedList.push.apply(this.generatedList, chunk);
        });
      }
    }
  }
};
</script>
<style scoped>
.v-text-field {
  width: 200px;
}
</style>
