<template>
  <v-btn
    class="mx-2 ml-10"
    icon
    color="primary"
    @click="downloadData"
    :disabled="this.uuidsList.length === 0"
  >
    <v-icon>mdi-download</v-icon>
  </v-btn>
</template>

<script>
export default {
  name: "ExportToCsv",
  props: {
    uuidsList: {
      type: Array,
      default: () => []
    },
    exportFileName: {
      type: String,
      default: "Last 5 UUID's"
    }
  },
  data() {
    return {};
  },
  methods: {
    downloadData() {
      let csvContent = "data:text/csv;charset=utf-8,";
      csvContent += "Last 5 UUID's, \n";
      csvContent += this.uuidsList.map(uid => uid + ",").join("\n");
      const link = document.createElement("a");
      link.setAttribute("href", csvContent);
      link.setAttribute("download", "uuids.csv");
      link.click();
    }
  }
};
</script>

