<template>
  <v-progress-linear :value="progressValue" :active="show"></v-progress-linear>
</template>

<script>
export default {
  name: "LinearProgressBar",
  props: {
    max: {
      type: Number,
      default: 5
    }
  },
  data() {
    return {
      interval: {},
      value: 0,
      show: true
    };
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  mounted() {
    this.interval = setInterval(() => {
      if (this.value >= this.max) {
        this.$emit("timer-elapsed");
        return (this.value = 0);
      }
      this.value += 1;
    }, 1000);
  },
  computed: {
    progressValue() {
      return this.value * (100 / this.max);
    }
  }
};
</script>

