<template>
  <v-container class="lighten-3 mt-6">
    <v-row cols="2">
      <v-col cols="6">
        <v-card>
          <v-card-title class="headline">
            {{ title }}
          </v-card-title>
          <v-card-text>
            <div>
              New UUID every
              <v-btn
                v-for="t in times"
                :key="t"
                color="primary"
                small
                class="mx-2"
                @click="setNewTime(t)"
                >{{ t }}</v-btn
              >
              seconds
            </div>
            <div>
              <v-switch v-model="uuidUpperCase" label="Uppercase?" class="hundredpx"></v-switch>
            </div>
            <br />
            <div class="pt-2 d-flex align-content-space-between">
              <div>
                <v-card class="pa-2" outlined tile>
                  {{ uuidUpperCase ? guid.toUpperCase() : guid.toLowerCase() }}
                </v-card>
                <linear-progress-bar
                  @timer-elapsed="timerElapsed"
                  :max="time"
                ></linear-progress-bar>
              </div>
              <v-btn v-clipboard="uuidUpperCase ? guid.toUpperCase() : guid.toLowerCase()" class="ml-4 mt-1 primary">
                Copy
                <v-icon right size="17">mdi-content-copy</v-icon>
              </v-btn>
            </div>
          </v-card-text>
        </v-card>
        <br />
        <last-five-uuids :uuids-list="uuidsList" />
      </v-col>
      <v-col cols="5">
        <bulk-export />
      </v-col>
    </v-row>
    <v-row cols="2" class="ml-0">
      <v-card>
        <v-card-subtitle class="font-weight-bold">Why Us?</v-card-subtitle>
        <v-card-text>
          <why-us />
        </v-card-text>
      </v-card>
    </v-row>
    <v-row>
      <br />
    </v-row>
    <v-row cols="2" class="ml-0">
      <disclaimer />
    </v-row>
  </v-container>
</template>

<script>
import uuid from "uuid/v4";
import LinearProgressBar from "./LinearProgressBar";
import LastFiveUuids from "./LastFiveUuids";
import BulkExport from "./BulkExport";
import Disclaimer from "./Disclaimer";
import WhyUs from "./WhyUs";

export default {
  name: "AutoGuid",
  components: {
    LinearProgressBar,
    LastFiveUuids,
    BulkExport,
    Disclaimer,
    WhyUs
  },
  metaInfo: {
    title: "Auto UUID's and Bulk Export",
    meta: [
      { "http-equiv": "Content-Type", content: "text/html; charset=utf-8"},
      { name: "description", content: "FREE and Easy way to Auto generate universally unique identifiers (UUIDs) either single or bulk. We also provide the ability to export to CSV."},
      { name: "keywords", content: "guid, uuid, guid online, guid generator, uuid online, uuid generator, onlineuuid, onlineuuid, universally unique identifier, js guid, auto guid generator, auto uuid generator, auto uuid, auto guid, unique identifier, unique id generator, auto unique id"},

      // OpenGraph data (Most widely used)
      {property: "og:title", content: "Auto UUID's and Bulk Export"},
      {property: "og:site_name", content: "Auto UUID Generator"},
      // The list of types is available here: http://ogp.me/#types
      {property: "og:type", content: "website"},
      // Should the the same as your canonical link, see below.
      // {property: "og:url", content: "https://www.my-site.com/my-special-page"},
      // {property: "og:image", content: "https://www.autouuidgen.com/my-special-image.jpg"},
      // Often the same as your meta description, but not always.
      {property: "og:description", content: "FREE and Easy way to Auto generate universally unique identifiers (UUIDs) either single or bulk export. We also provide the ability to export to CSV."},

      // Twitter card
      {name: 'twitter:card', content: 'summary'},
      // {name: 'twitter:site', content: 'https://www.my-site.com/my-special-page'},
      {name: 'twitter:title', content: "Auto UUID's and Bulk Export"},
      {name: 'twitter:description', content: 'FREE and Easy way to Auto generate universally unique identifiers (UUIDs) either single or bulk export. We also provide the ability to export to CSV.'},
      // Your twitter handle, if you have one.
      {name: 'twitter:creator', content: '@nani938'},
      // {name: 'twitter:image:src', content: 'https://www.my-site.com/my-special-image.jpg'}
    ]
    // https://www.digitalocean.com/community/tutorials/vuejs-vue-seo-tips
    // https://v1.vuetifyjs.com/en/components/snackbars
  },
  data() {
    return {
      title: "Auto UUID generator",
      guid: uuid(),
      time: 3,
      times: [3, 5, 10, 15],
      uuidsList: [],
      uuidUpperCase: false
    };
  },
  methods: {
    timerElapsed() {
      this.adjustUuidsList();
      this.guid = this.uuidUpperCase
        ? uuid().toUpperCase()
        : uuid().toLowerCase();
    },
    adjustUuidsList() {
      const formattedId = this.uuidUpperCase
        ? this.guid.toUpperCase()
        : this.guid.toLowerCase();
      if (this.uuidsList.length > 4) {
        this.uuidsList.shift();
        this.uuidsList.push(formattedId);
      } else {
        this.uuidsList.push(formattedId);
      }
    },
    setNewTime(n) {
      this.time = Number(n);
    }
  }
};
</script>
<style scoped lang="scss">
.hundredpx {
  width: 100px;
}
</style>
